* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: black;
}

.topnav {
  position: relative;
  background-color: white;
  overflow: hidden;
  height: 100px;
  text-align: center;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 40px 50px;
  text-decoration: none;
  font-size: 17px;
  color: black
}

/* Centered section inside the top navigation */
.topnav-centered a {
  float: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Right-aligned section inside the top navigation */
.topnav-right {
  float: right;
}

/* Responsive navigation menu - display links on top of each other instead of next to each other (for mobile devices) */
@media screen and (max-width: 600px) {
  .topnav a, .topnav-right {
    float: none;
    display: block;
  }

  .topnav-centered a {
    position: relative;
    top: 0;
    left: 0;
    transform: none;
  }
}

a:link { text-decoration: none; }
a:visited { text-decoration: none; }
a:hover { text-decoration: none; }
a:active { text-decoration: none; }

#horizontal {
  height: 5px;
  background-color: black;
  border: none;
  margin: 0px;
}
h1 {
  font-family:Georgia, 'Times New Roman', Times, serif;
}

.icons {
  background: rgb(175,217,213);
  background: linear-gradient(90deg, rgba(175,217,213,1) 5%, rgba(117,179,207,1) 30%, rgba(119,208,207,1) 100%);
  padding: 25px;
  margin-top: 0px;
  text-align: center;
}
.icon:hover {
  box-shadow: 0 5px 30px #193130, 0 -5px 30px #ebe3db;
}
.row {
  display: flex;
  border-spacing: 80px;
  align-items: center;
  justify-content:center;
}
.icon {
  text-align: center;
  width: 125px;
  height: 85px;
  padding: 10px;
  border-radius: 25px;
  display: block;
  margin-inline: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: white;
  box-shadow: 5px 10px 8px #888888;
}
.instrucciones {
  display: flex;
  align-items: center;
  justify-content: center;
}
.instructions-container {
  background-color: transparent;
  width: 740px;
  height: 200px;
  /*border: 1px solid #f1f1f1;*/
  perspective: 1000px;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* This container is needed to position the front and back side */
.box {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  display: block;
  margin-right: 50px;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.box:hover .front {
  transform: rotateY(180deg);
}
.box:hover .back {
  transform: rotateY(0deg);
  border-radius: 50%;
}

/* Position the front and back side */
.front, .back {
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  transition: transform 1s;
}

/* Style the front side (fallback if image is missing) */
.front {
  position: absolute;
  background-color: white;
  color: black;
}

/* Style the back side */
.back {
  background-color: white;
  color: white;
  transform: rotateY(-180deg);
}
#text {
  justify-content: center;
  font-size: x-small;
}

footer {
  background-color: gray;
  text-align: center;
}
.footer, .copyright, .separators {
  font-size: 10px;
  color: white;
}
.copyright {
  margin-left: 95px;
}
.separators {
  display: inline;
}

/* botones registro trabajador */

#id_trabajos input[type="checkbox"] {
  display: none;
}

#id_trabajos span {
  display: inline-block;
  padding: 10px;
  text-transform: uppercase;
  border: 2px solid black;
  border-radius: 3px;
  color: black;
}

#id_trabajos input[type="checkbox"]:checked + span {
  background-color: black;
  color: white;
}

/* estilo del popup */

.modal-box{ font-family: 'Poppins', sans-serif; }
.show-modal{
  color: #fff;
  background: linear-gradient(to right, #33a3ff, #0675cf, #49a6fd);
  font-size: 18px;
  font-weight: 600;
  text-transform: capitalize;
  padding: 10px 15px;
  margin: 200px auto 0;
  border: none;
  outline: none;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  display: block;
  transition: all 0.3s ease 0s;
}
.show-modal:hover,
.show-modal:focus{
  color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  outline: none;
}
.modal-dialog{
  width: 400px;
  margin: 70px auto 0;
}
.modal-dialog{ transform: scale(0.5); }
.modal-dialog{ transform: scale(1); }
.modal-dialog .modal-content{
  text-align: center;
  border: none;
}
.modal-content .close{
  color: #fff;
  background: linear-gradient(to right, #33a3ff, #0675cf, #4cd5ff);
  font-size: 25px;
  font-weight: 400;
  text-shadow: none;
  line-height: 27px;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  overflow: hidden;
  opacity: 1;
  position: absolute;
  left: auto;
  right: 8px;
  top: 8px;
  z-index: 1;
  transition: all 0.3s;
}
.modal-content .close:hover{
  color: #fff;
  box-shadow: 0 0 5px rgba(0,0,0,0.5);
}
.close:focus{ outline: none; }
.modal-body{ padding: 60px 40px 40px !important; }
.modal-body .title{
  color: #026fd4;
  font-size: 33px;
  font-weight: 700;
  letter-spacing: 1px;
  margin: 0 0 10px;
}
.modal-body .description{
  color: #9A9EA9;
  font-size: 16px;
  margin: 0 0 20px;
}
.modal-body .form-group{
  text-align: left;
  margin-bottom: 20px;
  position: relative;
}
.modal-body .input-icon{
  color: #777;
  font-size: 18px;
  transform: translateY(-50%);
  position: absolute;
  top: 50%;
  left: 20px;
}
.modal-body .form-control{
  font-size: 17px;
  height: 45px;
  width: 100%;
  padding: 6px 0 6px 50px;
  margin: 0 auto;
  border: 2px solid #eee;
  border-radius: 5px;
  box-shadow: none;
  outline: none;
}
.form-control::placeholder{
  color: #AEAFB1;
}
.form-group.checkbox{
  width: 130px;
  margin-top: 0;
  display: inline-block;
}
.form-group.checkbox label{
  color: #9A9EA9;
  font-weight: normal;
}
.form-group.checkbox input[type=checkbox]{
  margin-left: 0;
}
.modal-body .forgot-pass{
  color: #7F7FD5;
  font-size: 13px;
  text-align: right;
  width: calc(100% - 135px);
  margin: 2px 0;
  display: inline-block;
  vertical-align: top;
  transition: all 0.3s ease 0s;
}
.forgot-pass:hover{
  color: #9A9EA9;
  text-decoration: underline;
}
.modal-body .register{
  color: #7F7FD5;
  font-size: 13px;
  text-align: center;
  width: calc(100% - 135px);
  margin: 2px 0;
  display: inline-block;
  vertical-align: top;
  transition: all 0.3s ease 0s;
}
.register:hover{
  color: #9A9EA9;
  text-decoration: underline;
}

.modal-content .modal-body .btn{
  color: #fff;
  background: linear-gradient(to right, #33a3ff, #0675cf, #4cd5ff);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
  line-height: 38px;
  width: 100%;
  height: 40px;
  padding: 0;
  border: none;
  border-radius: 5px;
  border: none;
  display: inline-block;
  transition: all 0.6s ease 0s;
}
.modal-content .modal-body .btn:hover{
  color: #fff;
  letter-spacing: 2px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.modal-content .modal-body .btn:focus{ outline: none; }
  @media only screen and (max-width: 480px){
    .modal-dialog{ width: 95% !important; }
    .modal-content .modal-body{
        padding: 60px 20px 40px !important;
  }
}